export default function GetTheme(name) {
  switch (name) {
    case themeName[0]:
      return (themeRed);
    case themeName[1]:
      return (themePink);
    case themeName[2]:
      return (themePurple);
    case themeName[3]:
      return (themeIndigo);
    case themeName[4]:
      return (themeBlue);
    case themeName[5]:
      return (themeLightBlue);
    case themeName[6]:
      return (themeCyan);
    case themeName[7]:
      return (themeTeal);
    case themeName[8]:
      return (themeGreen);
    case themeName[9]:
      return (themeLightGreen);
    case themeName[10]:
      return (themeLime);
    case themeName[11]:
      return (themeYellow);
    case themeName[12]:
      return (themeAmber);
    case themeName[13]:
      return (themeOrange);
    case themeName[14]:
      return (themeDeepOrange);
    default:
      return (themeRed);
  }
}
