import React from "react";
import { makeStyles, Theme, createStyles } from "@material-ui/core/styles";
import Stepper from "@material-ui/core/Stepper";
import Step from "@material-ui/core/Step";
import StepLabel from "@material-ui/core/StepLabel";
import StepContent from "@material-ui/core/StepContent";
import Button from "@material-ui/core/Button";
import Paper from "@material-ui/core/Paper";
import Typography from "@material-ui/core/Typography";
import { MediaBannerCard } from "./MediaBannerCard";
import ErrorRadios from "./ErrorRadios";
import Radio from "@material-ui/core/Radio";
import RadioGroup from "@material-ui/core/RadioGroup";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import FormControl from "@material-ui/core/FormControl";
import FormHelperText from "@material-ui/core/FormHelperText";
import FormLabel from "@material-ui/core/FormLabel";

import LinearProgress from "@material-ui/core/LinearProgress";
import FormSubmit from "./FormSubmit";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      width: "100%",
    },
    button: {
      marginTop: theme.spacing(1),
      marginRight: theme.spacing(1),
    },
    actionsContainer: {
      marginBottom: theme.spacing(2),
    },
    resetContainer: {
      padding: theme.spacing(3),
    },
    formControlA: {
      margin: theme.spacing(3),
    },
    buttonA: {
      margin: theme.spacing(1, 1, 0, 0),
    },
    rootA: {
      width: "100%",
      "& > * + *": {
        marginTop: theme.spacing(2),
      },
    },
  })
);

function getSteps() {
  return [
    "อินเทอร์เน็ตสรรพสิ่ง (Internet of Thing: IOT)",
    "ปัญญาประดิษฐ์ (Artificial Intelligence: AI)",
    "การประมวลผลแบบกลุ่มเมฆ (Cloud Computing)",
    "เทคโนโลยีสารสนเทศในชีวิตประจำวัน",
    "เทคโนโลยีสารสนเทศในชีวิตประจำวัน",
  ];
}

function getStepContent(step: number) {
  switch (step) {
    case 0:
      return (
        <div>
          <MediaBannerCard></MediaBannerCard>
        </div>
      );
    case 1:
      return `Try out different ad text to see what brings in the most customers,
              and learn how to enhance your ads using features like ad extensions.
              If you run into any problems with your ads, find out how to tell if
              they're running and how to resolve approval issues.`;
    case 3:
    case 2:
      return `Try out different ad text to see what brings in the most customers,
              and learn how to enhance your ads using features like ad extensions.
              If you run into any problems with your ads, find out how to tell if
              they're running and how to resolve approval issues.`;
    case 3:
      return `Try out different ad text to see what brings in the most customers,
              and learn how to enhance your ads using features like ad extensions.
              If you run into any problems with your ads, find out how to tell if
              they're running and how to resolve approval issues.`;
    default:
      return "Unknown step";
  }
}

export default function VerticalLinearStepper({setChildComplete}) {
  const classes = useStyles();
  const [activeStep, setActiveStep] = React.useState(0);
  const steps = getSteps();

  const [value, setValue] = React.useState("");
  const [error, setError] = React.useState(true);
  const [reSubmit, setResubmit] = React.useState(true);
  const [checked, setChecked] = React.useState(false);
  const [helperText, setHelperText] = React.useState("Choose wisely");


  const handleNext = () => {
    if(activeStep === steps.length - 1){
      setChildComplete(true);
    }
    setActiveStep((prevActiveStep) => prevActiveStep + 1);

    setHelperText("Please select an option.");
    setChecked(false);
    setError(true);
    setResubmit(true);
    setValue("");
  };

  const handleBack = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1);
  };

  const handleReset = () => {
    setActiveStep(0);
  };

  const handleRadioChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    setChecked(false);
    setValue(event.target.value);
    setHelperText(" ");
    setError(false);
  };

  const handleSubmit = (event: React.FormEvent<HTMLFormElement>) => {
    event.preventDefault();
    setChecked(false);
    if (value === "best") {
      setHelperText("You got it!");
      setError(false);
      setResubmit(false);
      setValue("");
    } else if (value === "worst") {
      setHelperText("Sorry, wrong answer!");
      setError(true);
      setResubmit(true);
      setCompleted(0);
      setBuffer(10);
      setValue("");
    } else {
      setHelperText("Please select an option.");
      setError(true);
    }
  };

  const handleCheckAnswer = (e) => {
    setChecked(true);
    if (value === "best") {
      setHelperText("You got it!");
      setError(false);
      setResubmit(false);
      setValue("");
    } else if (value === "worst") {
      setHelperText("Sorry, wrong answer!");
      setError(true);
      setResubmit(true);
      setCompleted(0);
      setBuffer(10);
      setValue("");
    } else {
      setHelperText("Please select an option.");
      setError(true);
      setValue("");
    }
  };

  const [completed, setCompleted] = React.useState(0);
  const [buffer, setBuffer] = React.useState(10);

  const progress = React.useRef(() => {});

  React.useEffect(() => {
    progress.current = () => {
      if (completed > 100) {
        setCompleted(0);
        setBuffer(10);
        setResubmit(false);
      } else {
        const diff = Math.random() * 10;
        const diff2 = Math.random() * 10;
        setCompleted(completed + diff);
        setBuffer(completed + diff + diff2);
      }
    };
  });

  React.useEffect(() => {
    function tick() {
      progress.current();
    }
    const timer = setInterval(tick, 500);

    return () => {
      clearInterval(timer);
    };
  }, []);

  return (
    <div className={classes.root}>
      <Stepper activeStep={activeStep} orientation="vertical">
        {steps.map((label, index) => (
          <Step key={label}>
            <StepLabel>{label}</StepLabel>
            <StepContent>
              <Typography>{getStepContent(index)}</Typography>
              <div className={classes.actionsContainer}>
                <div>
                  <FormControl
                    component="fieldset"
                    error={error}
                    className={classes.formControl}
                  >
                    <FormLabel component="legend">Check Point</FormLabel>
                    <RadioGroup
                      aria-label="quiz"
                      name="quiz"
                      value={value}
                      onChange={handleRadioChange}
                    >
                      <FormControlLabel
                        value="best"
                        control={<Radio />}
                        label="The best!"
                      />
                      <FormControlLabel
                        value="worst"
                        control={<Radio />}
                        label="The worst."
                      />
                    </RadioGroup>
                    <FormHelperText>{helperText}</FormHelperText>
                    {!error && !reSubmit ? (
                      checked ? (
                        <div>
                          <Button
                            variant="contained"
                            color="secondary"
                            disabled={activeStep === 0}
                            onClick={handleBack}
                            className={classes.button}
                          >
                            Back
                          </Button>
                          <Button
                            variant="contained"
                            color="primary"
                            onClick={handleNext}
                            className={classes.button}
                          >
                            {activeStep === steps.length - 1
                              ? "Finish"
                              : "Next"}
                          </Button>
                        </div>
                      ) : (
                        <div>
                          <Button
                            variant="outlined"
                            color="primary"
                            className={classes.button}
                            onClick={handleCheckAnswer}
                          >
                            Check Answer
                          </Button>
                        </div>
                      )
                    ) : (
                      <div className={classes.root}>
                        <LinearProgress
                          variant="buffer"
                          value={completed}
                          valueBuffer={buffer}
                          color="secondary"
                        />
                      </div>
                    )}
                  </FormControl>
                </div>
              </div>
            </StepContent>
          </Step>
        ))}
      </Stepper>
      {activeStep === steps.length && (
        <Paper square elevation={0} className={classes.resetContainer}>
          <Typography>All steps completed - you&apos;re finished</Typography>
          <Button onClick={handleReset} className={classes.button}>
            Reset
          </Button>
        </Paper>
      )}
    </div>
  );
}
