function getSteps() {
  return [
    "แนะนำรายวิชา",
    "วิทยาการคอมพิวเตอร์",
    "ข้อมูลและสารสนเทศ",
    "วิทยาการข้อมูล",
    "การเก็บรวบรวมข้อมูล",
    "การสำรวจข้อมูล",
    "การประมวลผลข้อมูล",
    "การวิเคราะห์ข้อมูล",
    "การสื่อสารด้วยข้อมูล",
  ];
}

function getStepContent(step: number, setChildComplete) {
  switch (step) {
    case 0:
      return <VerticalLinearStepper setChildComplete={setChildComplete}></VerticalLinearStepper>;
    case 1:
      return "What is an ad group anyways?";
    case 2:
      return "This is the bit I really care about!";
    case 3:
      return "This is the bit I really care about!";
    case 4:
      return "This is the bit I really care about!";
      case 5:
        return "What is an ad group anyways?";
      case 6:
        return "This is the bit I really care about!";
      case 7:
        return "This is the bit I really care about!";
      case 8:
        return "This is the bit I really care about!";
    default:
      return "Unknown step";
  }
}

export default function CustomizedSteppers({ data, accessToken }) {
  console.log(data);
  const classes = useStyles();
  const [activeStep, setActiveStep] = React.useState(parseInt(data.allTask));
  const steps = getSteps();
  const [isChildComplete, setChildComplete] = React.useState(parseInt(false));

  const handleNext = () => {
    setActiveStep((prevActiveStep) => prevActiveStep + 1);
  };

  const handleBack = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1);
  };

  const handleReset = () => {
    setActiveStep(0);
  };

  const sendMessage = () => {
    handleNext();
    const formData = new FormData()
    var steps_name = activeStep+1;
    formData.append(GOOGLE_FORM_EMAIL_ID, data.studEmail)
    formData.append(GOOGLE_FORM_TASK_ID, "task0".concat(steps_name.toString()))
    formData.append(GOOGLE_FORM_LOG_ID, accessToken)
    axios.post(CORS_PROXY + GOOGLE_FORM_ACTION,formData)
    .catch(() => {
        
      })
  }


  return (
    <div className={classes.root}>
      <Stepper
        alternativeLabel
        activeStep={activeStep}
        connector={<ColorlibConnector />}
      >
        {steps.map((label) => (
          <Step key={label}>
            <StepLabel StepIconComponent={ColorlibStepIcon}>{label}</StepLabel>
          </Step>
        ))}
      </Stepper>
      <div>
        {activeStep === steps.length ? (
          <div>
            <Typography className={classes.instructions}>
              All steps completed - you&apos;re finished
            </Typography>
            <Button onClick={handleReset} className={classes.button}>
              Reset
            </Button>
          </div>
        ) : (
          <div>
            <Typography className={classes.instructions}>
              {getStepContent(activeStep, setChildComplete)}
            </Typography>
            
            <div>
              <Button
                disabled={activeStep === 0}
                onClick={handleBack}
                variant="contained"
                color="secondary"
                className={classes.button}
              >
                Back
              </Button>
              { isChildComplete ?
                <Button
                variant="contained"
                color="primary"
                onClick={sendMessage}
                className={classes.button}
              > 
              {
                  activeStep === steps.length - 1 ? 
                  "Finish" : "Next"
              }
              </Button>
              : 
              <div></div>
                }
            </div>
          </div>
        )}
      </div>
    </div>
  );
}
